<template>
  <v-app id="inspire">
    <v-navigation-drawer app v-model="drawer">
      <div class="todo-app-menu pa-3">
        <v-list dense nav>
          <v-btn block large color="primary" class="mb-8 text-capitalize" @click="openDialog">
            Add Task
          </v-btn>
          <v-list-item-group color="primary">
            <v-list-item link class="mb-3 align-center">
              <v-list-item-icon>
                <v-icon color="primary">mdi-checkbox-marked-circle-outline</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Task</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-badge inline color="primary" :content="items.length" :value="items.length"></v-badge>
              </v-list-item-action>
            </v-list-item>
            <v-list-item link>
              <v-list-item-icon>
                <v-icon color="success">mdi-check</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Completed</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-badge inline color="primary" :content="completed.length" :value="completed.length"></v-badge>
              </v-list-item-action>
            </v-list-item>
            <v-subheader><span class="text-uppercase">Labels</span></v-subheader>
            <v-list-item link class="mb-3">
              <v-list-item-icon>
                <v-icon color="primary">mdi-label-outline</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Work</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item link>
              <v-list-item-icon>
                <v-icon color="success">mdi-label-outline</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Fun</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </div>
    </v-navigation-drawer>

    <v-btn fixed fab bottom right elevation="5" color="primary" class="d-md-block d-lg-none" @click="openDialog">
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <v-app-bar app elevation="0" color="white">
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>My Todo</v-toolbar-title>
    </v-app-bar>

    <v-main>
      <v-container>
        <v-row>
          <v-col cols="12">
            <v-card elevation="0" >
              <v-list subheader two-line flat>
                <v-list-item-group multiple v-for="(item, index) in items" :key="index">
                  <v-list-item> 
                    <template v-slot:default>
                      <v-list-item-action @click="doneTask(item, index)">
                        <v-checkbox :input-value="item.done" color="primary">
                        </v-checkbox>
                      </v-list-item-action>

                      <v-list-item-content @click="editItem(item)">
                        <v-list-item-title :class="{ 'text-decoration-line-through primary--text' : item.done }">{{item.title ? item.title : '-'}}</v-list-item-title>
                        <v-list-item-subtitle :class="{ 'text-decoration-line-through primary--text' : item.done }">{{item.description}}</v-list-item-subtitle>
                      </v-list-item-content>

                      <v-list-item-action @click="removeItem(index)">
                        <v-icon color="primary lighten-3">mdi-delete</v-icon>
                      </v-list-item-action>
                    </template>
                  </v-list-item>
                </v-list-item-group>
                <v-sheet height="550" class="d-flex align-center text-center">
                  <v-list-item v-show="!items.length">
                    <v-list-item-title>Time to relax, no tasks on your list today! 😎 </v-list-item-title>
                  </v-list-item>
                </v-sheet>
              </v-list>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>

    <v-dialog width="600" v-model="dialog">
      <v-card>
        <v-toolbar elevation="0">
          <v-toolbar-title class="text-h6">{{dialogTitle}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog = !dialog">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>

        <v-divider></v-divider>

        <v-container class="pa-5">
          <v-row>
            <v-col cols="12">
              <v-text-field
                v-model="formData.title"
                :rules="[rules.required]"
                color="primary"
                label="Title">
              </v-text-field>
              <v-textarea 
                v-model="formData.description"
                color="primary"
                label="Description">
              </v-textarea>
            </v-col>
          </v-row>
        </v-container>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" class="text-capitalize px-8" @click="saveData">
            Save
          </v-btn>
        </v-card-actions>

      </v-card>
    </v-dialog>
  </v-app>  
</template>

<script>
  export default {
    name: 'todo',
    data: () => ({
      dialog: false,
      dialogTitle: 'Add Task',
      cards: ['Today', 'Yesterday'],
      drawer: null,
      items: [],
      completed: [],
      formData: {
        id: '',
        title: '',
        description: '',
        done: false
      },
      rules: {
        required: value => !!value || 'Required.'
      }
    }),

    mounted() {
      this.getData();
    },

    methods: {
      doneTask(val, index) {
        let task = this.items.filter(e => e.id === val.id)[0]
        task.done = !task.done
        if (!val.done) {
          // setTimeout(() => {
            this.completed.splice(index, 1)
          // }, 1000);
        } else {
          // setTimeout(() => {
            this.completed.push({
              id: val.id,
              title : val.title,
              description: val.description,
              done: val.done
            })
            // this.items.splice(index, 1)
          // }, 1000);
        }
      },

      saveData() {
        let uuid = Math.random().toString(36).substr(2, 9);
        this.items.push({
          id : uuid,
          title : this.formData.title,
          description: this.formData.description,
          done: false
        })
        localStorage.setItem('data', JSON.stringify(this.items))
        this.dialog = false
      },

      getData() {
        this.items = JSON.parse(localStorage.getItem('data'))
        if (this.items == null) {
          this.items = []
        }
      },

      openDialog() {
        this.dialog = true
        this.formData = {},
        this.dialogTitle = 'Add Task'
      },

      editItem(val) {
        this.dialog = true
        this.formData = val
        this.dialogTitle = 'Edit Task'
      },

      removeItem(index) {
        this.items.splice(index, 1)
        localStorage.setItem('data', JSON.stringify(this.items))
      }
    }
  }
</script>

<style>
.theme--light.v-application {
  background-color: #f2f5f8;
}
</style>